
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import loginArigin from '@/components/loginArigin.vue'
import {sendSMSCode, login} from '@/api/user.js'
import { getToken } from '@/utils/auth'

export default {
name: 'loginIndex',
components: {
XInput,
Group,
XButton,
loginArigin
},
data () {
return {
getCodeStatus: false,
countdown: 60,
postData: {
mob: '',
way: 'pwd',
pwd: '',
piccode : '',
},
piccode:'',
}
},
created () {
this.getPicCode();
},
methods: {
getPicCode(){
this.piccode = process.env.BASE_API+'/Api/System/getPicCode&token=' + getToken() + ('&random=' + Math.random())
},


sendSMSCode () {
if (this.getCodeStatus) {

return
}




const reg = 11 && /^((13|14|15|16|17|18|19)[0-9]{1}\d{8})$/
if (!reg.test(this.postData.mob)) {
if (this.postData.mob === '' || this.postData.mob === null) {
this.$vux.toast.text('请填写手机号', 'middle')
return
}
this.$vux.toast.text('手机号有误')
return
}
if (this.postData.piccode === '' || this.postData.piccode === null) {
this.$vux.toast.text('请填写图形验证码', 'middle')
return
}
const postData = {
mob: this.postData.mob,
piccode:this.postData.piccode,
type: 1 
}
const _this = this
sendSMSCode(postData)
.then(res => {
if (res.data.errorcode === 0) {

_this.$vux.toast.text('已发送,请注意查收！', 'middle')
_this.getCodeStatus = true
let interVal = setInterval(function () {
if (_this.getCodeStatus && _this.countdown > 0) {
_this.countdown--
} else {
_this.getCodeStatus = false
clearInterval(interVal)
_this.countdown = 60
}
}, 1000)
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
console.log(err)
this.$vux.toast.text('网络异常！', 'middle')
})
},

handleLogin () {
console.log(this.postData)

const reg = 11 && /^((13|14|15|16|17|18|19)[0-9]{1}\d{8})$/
if (!reg.test(this.postData.mob)) {
this.$vux.toast.text('手机号有误')
return
}
if (this.postData.pwd === '') {
if (this.postData.way === 'pwd') {
this.$vux.toast.text('请输入密码')
} else if (this.postData.way === 'code') {
this.$vux.toast.text('请输入短信验证码')
}
return
}
if (this.postData.mob === '' || this.msgCode === '' || this.repwd === '' || this.postData.pwd === '') {
this.$vux.toast.text('请填写完整信息')
return
}

login(this.postData)
.then(res => {
if (res.data.errorcode === 0) {

this.$vux.toast.text('登录成功', 'middle')

localStorage.setItem('accountIsLogin', true)
localStorage.setItem('isLogin', true)
localStorage.setItem('shareid', res.data.data.shareid)

if (localStorage.getItem('tickUrl')) {
const url = localStorage.getItem('tickUrl')
console.log('跳转')

localStorage.removeItem('tickUrl')
this.$router.replace({path: url})
return
}



if (localStorage.getItem('beforeLoginPath') !== '/register') {
if (localStorage.getItem('beforeLoginPath') !== '/confirmOrder' && localStorage.getItem('beforeLoginPath') !== '/shoppingtrolley' && res.data.data.issubscribed === 0) {
console.log(this.$vux.confirm.show)
this.$vux.confirm.show({
title: '关注公众号可第一时间获取订单动态',
content: '<img src="' + res.data.data.qrcodeurl + '" width="100%"/>',
showCancelButton: false,
confirmText: '暂不关注',



onConfirm () {
}
})
}
this.$router.replace({path: localStorage.getItem('beforeLoginFullpath')})
} else {
this.$router.replace({path: '/usercenter'})
}
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
console.log(err)
this.$vux.toast.text('网络异常！', 'middle')
})
}
}
}



